import BarberMixin from '@/mixins/BarberMixin'
export default {
  mixins: [BarberMixin],
  methods: {
    displayNotification(userType, notificationType, notificationData) {
      if (userType === 'barber') {
        if (this.barberState) {
          if (notificationType === 'newSchedule') {
            return Number(this.barberState.id) === Number(notificationData.barber_id) ? true : false
          }
        }
        return false
      }

      return true
    },
    notify(title, body) {
      const userType = JSON.parse(body).userType
      const notificationData = JSON.parse(body).data
      const notificationType = JSON.parse(body).notificationType

      if (this.displayNotification(userType, notificationType, notificationData)) {
        const description = JSON.parse(body).description
        const notification = new Notification(title, {
          body: description,
          icon: '/icone-180x180.png',
          badge: '/icone-180x180.png',
          vibrate: true
        });

        if (body.link) {
          notification.addEventListener('click', () => {
            const link = JSON.parse(body).link
            window.location.href = link
          });
        }
      }
    }
  }
}