import firebase from 'firebase';
import PushNotificationMixin from './PushNotificationMixin'
export default {
  mixins: [PushNotificationMixin],
  methods: {
    initFirebase() {
      if (!firebase.apps.length) {
        const firebaseConfig = {
          apiKey: "AIzaSyAtUkobfl5TyJdLk27CUTqyOl7v5gbzZeQ",
          authDomain: "arched-sunbeam-302621.firebaseapp.com",
          projectId: "arched-sunbeam-302621",
          storageBucket: "arched-sunbeam-302621.appspot.com",
          messagingSenderId: "69364991955",
          appId: "1:69364991955:web:144549be9ff79db1aa1126"
        };
        firebase.initializeApp(firebaseConfig);
      } else {
        firebase.app(); // if already initialized, use that one
      }
      const messaging = firebase.messaging();
      messaging.getToken().then(deviceToken => this.firebaseInicializado(deviceToken));
      const self = this
      messaging.onMessage(function (payload) {
        self.notify(payload.notification.title, payload.notification.body)
      });
    },
    firebaseInicializado(deviceToken) {
      window.console.log(`firebase inicializado: ${deviceToken}`)
    },
    saveBarberDeviceToken(barberId, deviceToken) {
      this.$axios.post(`${this.$url_api}/barber/save-device-token`, {
        'barber_id': barberId,
        'device_token': deviceToken
      })
    }
  }
}